.horizontalMenu {
	>.horizontalMenu-list>li {
		>ul.sub-menu {
			opacity: 0;
			visibility: hidden;
			-o-transform-origin: 0% 0%;
			-ms-transform-origin: 0% 0%;
			-moz-transform-origin: 0% 0%;
			-webkit-transform-origin: 0% 0%;
			-o-transition: -o-transform 0.3s, opacity 0.3s;
			-ms-transition: -ms-transform 0.3s, opacity 0.3s;
			-moz-transition: -moz-transform 0.3s, opacity 0.3s;
			-webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
			transform-style: preserve-3d;
			-o-transform-style: preserve-3d;
			-moz-transform-style: preserve-3d;
			-webkit-transform-style: preserve-3d;
			transform: rotateX(-75deg);
			-o-transform: rotateX(-75deg);
			-moz-transform: rotateX(-75deg);
			-webkit-transform: rotateX(-75deg);
		}
		&:hover {
			>ul.sub-menu,
			>.horizontal-megamenu {
				opacity: 1;
				visibility: visible;
				transform: rotateX(0deg);
				-o-transform: rotateX(0deg);
				-moz-transform: rotateX(0deg);
				-webkit-transform: rotateX(0deg);
			}
		}
		> {
			ul.sub-menu>li {
				>ul.sub-menu {
					opacity: 0;
					visibility: hidden;
					transform-style: preserve-3d;
					-o-transform-style: preserve-3d;
					-moz-transform-style: preserve-3d;
					-webkit-transform-style: preserve-3d;
					transform: rotateX(-75deg);
					-o-transform: rotateX(-75deg);
					-moz-transform: rotateX(-75deg);
					-webkit-transform: rotateX(-75deg);
				}
				&:hover>ul.sub-menu {
					opacity: 1;
					visibility: visible;
					-o-transform-origin: 0% 0%;
					-ms-transform-origin: 0% 0%;
					-moz-transform-origin: 0% 0%;
					-webkit-transform-origin: 0% 0%;
					-o-transition: -o-transform 0.4s, opacity 0.4s;
					-ms-transition: -ms-transform 0.4s, opacity 0.4s;
					-moz-transition: -moz-transform 0.4s, opacity 0.4s;
					-webkit-transition: -webkit-transform 0.4s, opacity 0.4s;
					transform: rotateX(0deg);
					-o-transform: rotateX(0deg);
					-moz-transform: rotateX(0deg);
					-webkit-transform: rotateX(0deg);
				}
				>ul.sub-menu>li {
					>ul.sub-menu {
						opacity: 0;
						visibility: hidden;
						-o-transform-origin: 0% 0%;
						-ms-transform-origin: 0% 0%;
						-moz-transform-origin: 0% 0%;
						-webkit-transform-origin: 0% 0%;
						-o-transition: -o-transform 0.4s, opacity 0.4s;
						-ms-transition: -ms-transform 0.4s, opacity 0.4s;
						-moz-transition: -moz-transform 0.4s, opacity 0.4s;
						-webkit-transition: -webkit-transform 0.4s, opacity 0.4s;
						transform-style: preserve-3d;
						-o-transform-style: preserve-3d;
						-moz-transform-style: preserve-3d;
						-webkit-transform-style: preserve-3d;
						transform: rotateX(-75deg);
						-o-transform: rotateX(-75deg);
						-moz-transform: rotateX(-75deg);
						-webkit-transform: rotateX(-75deg);
					}
					&:hover>ul.sub-menu {
						opacity: 1;
						visibility: visible;
						transform: rotateX(0deg);
						-o-transform: rotateX(0deg);
						-moz-transform: rotateX(0deg);
						-webkit-transform: rotateX(0deg);
					}
				}
			}
			.horizontal-megamenu {
				opacity: 0;
				visibility: hidden;
				-o-transform-origin: 0% 0%;
				-ms-transform-origin: 0% 0%;
				-moz-transform-origin: 0% 0%;
				-webkit-transform-origin: 0% 0%;
				-o-transition: -o-transform 0.3s, opacity 0.3s;
				-ms-transition: -ms-transform 0.3s, opacity 0.3s;
				-moz-transition: -moz-transform 0.3s, opacity 0.3s;
				-webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
				transform-style: preserve-3d;
				-o-transform-style: preserve-3d;
				-moz-transform-style: preserve-3d;
				-webkit-transform-style: preserve-3d;
				transform: rotateX(-75deg);
				-o-transform: rotateX(-75deg);
				-moz-transform: rotateX(-75deg);
				-webkit-transform: rotateX(-75deg);
			}
		}
	}
	html,
	body,
	iframe,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 0;
		padding: 0;
		border: 0;
		font: inherit;
		vertical-align: baseline;
		font-weight: normal;
		font-size: 12px;
		line-height: 18px;
		font-family: Helvetica, sans-serif;
		-webkit-font-smoothing: subpixel-antialiased;
		font-smoothing: antialiased;
		font-smooth: antialiased;
		-webkit-text-size-adjust: 100%;
		-ms-text-size-adjust: 100%;
		-webkit-font-smoothing: subpixel-antialiased;
		font-smoothing: subpixel-antialiased;
		font-smooth: subpixel-antialiased;
	}
	.cl {
		clear: both;
	}
	img {
		border: 0 none;
		max-width: 100%;
	}
}

object,
embed,
video {
	border: 0 none;
	max-width: 100%;
}

.horizontalMenu {
	a:focus {
		outline: none;
	}
	&:before {
		content: "";
		display: table;
	}
	&:after {
		content: "";
		display: table;
		clear: both;
	}
}


/* ================== Desktop Main Menu CSS ================== */

.horizontalMenucontainer {
	background-size: cover;
	overflow: hidden;
	background-attachment: fixed;
	background-position: 50% 0;
	background-repeat: no-repeat;
}

.horizontal-main {
	width: 100%;
	position: relative;
}

.horizontal-mainwrapper {
	margin: 0 auto;
}

.desktoplogo {
	margin: 0;
	float: $float-left;
	img {
		width: 100%;
		height:2.5rem;
	}
}

.horizontalMenu {
	color: $white;
	font-size: 14px;
	padding: 0;
	float: $float-right;
	display: block;
	>.horizontalMenu-list {
		text-align: start;
		margin: 0 auto 0 auto;
		width: 100%;
		display: block;
		padding: 0;
		>li {
			text-align: center;
			display: block;
			padding: 0;
			margin: 0;
			float: $float-left;
			padding: 0.5rem 0;
			>a {
				display: block;
				padding: 9px 15px;
				text-decoration: none;
				position: relative;
				margin: 0 2px;
				border-radius: 3px;
			}
			&:last-child>a {
				border-inline-end: 0px;
			}
			a.menuhomeicon {
				padding-inline-start: 25px;
				padding-inline-end: 25px;
			}
			>a {
				.fa {
					display: inline-block;
					font-size: 14px;
					line-height: inherit;
					margin-inline-end: 7px;
					vertical-align: top;
					margin-block-start: 2px;
					&.fa-home {
						font-size: 15px;
					}
				}
				.arrow:after {
					border-inline-start: 4px solid rgba(0, 0, 0, 0);
					border-inline-end: 4px solid rgba(0, 0, 0, 0);
					border-block-start: 4px solid;
					content: "";
					float: $float-right;
					 inset-inline-end: 11px;
					height: 0;
					margin: 0 0 0 14px;
					position: absolute;
					text-align: end;
					top: 33px;
					width: 0;
				}
			}
			&.rightmenu {
				float: $float-right;
				a {
					padding: 0px 30px 0px 20px;
					border-inline-end: none;
					.fa {
						font-size: 15px;
					}
				}
				float: $float-right;
				&:before {
					content: "";
					display: table;
				}
			}
		}
	}
}


/* Desktop Search Bar */

.horizontalMenu-list>li.rightmenu:after {
	content: "";
	display: table;
}

.horizontalMenu>.horizontalMenu-list>li {
	&.rightmenu {
		&:after {
			clear: both;
		}
		>.topmenusearch {
			float: $float-right;
			width: 210px;
			height: 39px;
			position: relative;
			margin: 16px 0px 0px 0px;
			.searchicon {
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;
			}
			input {
				width: 100%;
				position: relative;
				float: $float-right;
				top: 0;
				 inset-inline-end: 0;
				bottom: 0;
				width: 100%;
				border: 0;
				padding: 0;
				margin: 0;
				text-indent: 15px;
				height: 39px;
				z-index: 2;
				outline: none;
				color: #333;
				background-color: #efefef;
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;
				font-size: 12px;
				&::placeholder {
					color: #a9a9a9;
				}
				&:focus {
					color: #333;
					width: 230px;
					~ {
						.btnstyle .fa {
							color: $black;
							opacity: 1;
						}
						.searchicon {
							opacity: 1;
							z-index: 3;
							color: #FFFFFF;
						}
					}
				}
			}
			.btnstyle {
				top: 0px;
				position: absolute;
				 inset-inline-end: 0;
				bottom: 0;
				width: 40px;
				line-height: 30px;
				z-index: 1;
				cursor: pointer;
				opacity: 0.3;
				color: #333;
				z-index: 1000;
				background-color: transparent;
				border: solid 0px;
				-webkit-transition: all 0.7s ease 0s;
				-moz-transition: all 0.7s ease 0s;
				-o-transition: all 0.7s ease 0s;
				transition: all 0.7s ease 0s;
				.fa {
					line-height: 37px;
					margin: 0;
					padding: 0;
					text-align: center;
				}
				&:hover .fa {
					opacity: 1;
				}
			}
		}
		zoom: 1;
	}
	> {
		ul.sub-menu {
			position: absolute;
			top: 55px;
			z-index: 1000;
			margin: 0px;
			padding: 5px 0;
			min-width: 190px;
			background-color: $white;
			border: 1px solid $border;
			border-radius: 2px;
			box-shadow: 0px 5px 25px rgba(39, 55, 76, 0.2);
			>li {
				position: relative;
				margin: 0px;
				padding: 0px;
				display: block;
				> {
					a {
						background-image: none;
						color: #6c6a8d;
						border-inline-end: 0 none;
						text-align: start;
						display: block;
						line-height: 22px;
						padding: 8px 12px;
						text-transform: none;
						font-size: 14px;
						letter-spacing: normal;
						border-inline-end: 0px solid;
						&:hover {
							text-decoration: none;
						}
						>.fa {
							margin-inline-end: 9px;
						}
					}
					ul.sub-menu {
						min-width: 220px;
						position: absolute;
						inset-inline-start: 100%;
						top: 0;
						margin: 0;
						padding: 5px 0;
						list-style: none;
						background-color: $white;
						border: 1px solid $border;
						box-shadow: 0px 20px 25px rgb(39 55 76 / 20%);
						>li {
							position: relative;
							margin: 0px;
							padding: 0px;
							display: block;
							> {
								a {
									background-image: none;
									color: #6c6a8d;
									border-inline-end: 0 none;
									text-align: start;
									display: block;
									line-height: 22px;
									padding: 8px 12px;
									text-transform: none;
									font-size: 14px;
									letter-spacing: normal;
									border-inline-end: 0px solid;
									&:hover {
										text-decoration: none;
									}
									>.fa {
										margin-inline-end: 9px;
									}
								}
								ul.sub-menu {
									min-width: 220px;
									position: absolute;
									inset-inline-start: 100%;
									top: 0;
									margin: 0px;
									list-style: none;
									padding: 0px;
									background-color: $white;
									border: 1px solid $border;
									box-shadow: 0px 5px 25px rgba(39, 55, 76, 0.2);
									>li {
										position: relative;
										margin: 0px;
										padding: 0px;
										display: block;
										>a {
											background-image: none;
											color: #6c6a8d;
											border-inline-end: 0 none;
											text-align: start;
											display: block;
											line-height: 22px;
											padding: 8px 12px;
											text-transform: none;
											font-size: 13px;
											letter-spacing: normal;
											border-inline-end: 0px solid;
											&:hover {
												text-decoration: none;
											}
											>.fa {
												margin-inline-end: 9px;
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.horizontal-megamenu {
			width: 100%;
			inset-inline-start: 0px;
			position: absolute;
			top: 55px;
			color: $black;
			z-index: 1000;
			margin: 0px;
			text-align: start;
			font-size: 15px;
			.megamenu-content {
				border: 1px solid $border;
				background-color: $white;
				padding: 14px 20px;
				border-radius: 2px;
				box-shadow: 0px 5px 25px rgba(39, 55, 76, 0.2);
			}
			.title {
				padding: 5px 5px 5px 0px;
				font-size: 16px;
				color: #504e70;
				margin: 0px 0px 4px 0px;
				text-align: start;
				font-weight: 600;
			}
			.wsmwnutxt {
				width: 100%;
				font-size: 14px;
				text-align: justify;
				line-height: 22px;
				color: #504e70;
				margin-block-start: 10px;
			}
			.link-list li {
				display: block;
				text-align: center;
				white-space: nowrap;
				text-align: start;
				padding: 5px 10px 5px 10px;
				a {
					line-height: 18px;
					border-inline-end: none;
					text-align: start;
					padding: 6px 0px;
					background: $white;
					background-image: none;
					color: #6c6a8d;
					border-inline-end: 0 none;
					display: block;
					background-color: $white;
					font-size: 14px;
				}
			}
			li {
				.fa {
					margin-inline-end: 5px;
					text-align: center;
					width: 18px;
				}
				a:hover {
					background: transparent;
					text-decoration: none;
				}
			}
			.link-list li .fa {
				font-size: 12px;
			}
			li .fa {
				margin-inline-end: 5px;
				text-align: center;
				width: 18px;
			}
			.mrgtop {
				margin-block-start: 15px;
			}
			.show-grid div {
				padding-block-end: 10px;
				padding-block-start: 10px;
				background-color: #dbdbdb;
				border: 1px solid #e7e7e7;
				color: #6a6a6a;
				margin: 2px 0px;
				font-size: 13px;
			}
			&.halfmenu {
				width: 30%;
				 inset-inline-end: auto;
				inset-inline-start: auto;
			}
			&.halfdiv {
				width: 20%;
				 inset-inline-end: auto;
				inset-inline-start: auto;
			}
			.menu_form {
				width: 100%;
				display: block;
