					width: 100%;
					border: 1px solid #e2e2e2;
					color: $black;
					font-size: 13px;
					padding: 8px 5px;
					margin-block-end: 8px;
				}
				textarea {
					width: 100%;
					border: 1px solid #e2e2e2;
					color: $black;
