		position: absolute;
		top: -6px;
		inset-inline-start: 12px;
		display: inline-block;
		border-inline-end: 5px solid transparent;
		border-block-end: 5px solid rgba(0, 40, 100, 0.12);
		border-inline-start: 5px solid transparent;
		border-bottom-color: rgba(0, 40, 100, 0.12);
		content: '';
	}
	.sub-menu {
		.sub-menu {
			&:before,
			&:after {
				display: none;
			}
		}
		&:after {
			position: absolute;
			top: -5px;
			inset-inline-start: 12px;
			display: inline-block;
			border-inline-end: 5px solid transparent;
			border-block-end: 5px solid $white;
			border-inline-start: 5px solid transparent;
			content: '';
		}
	}
	.halfdiv:after {
		position: absolute;
		top: -5px;
		inset-inline-start: 12px;
		display: inline-block;
		border-inline-end: 5px solid transparent;
		border-block-end: 5px solid $white;
		border-inline-start: 5px solid transparent;
		content: '';
	}
	.sub-menu {
		&:before,
		&:after {
			inset-inline-start: 12px;
			 inset-inline-end: auto;
		}
	}
	.halfdiv {
		&:before,
		&:after {
			inset-inline-start: 12px;
			 inset-inline-end: auto;
		}
	}
